
.film_wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 160px;
  cursor: pointer;
  

  &:hover {
    opacity: 0.8;
  }

  .film_logo {
    height: 240px;
    width: 160px;
    border-radius: 5px;
  }
  
  .film_title {
    margin: 6px 0px;
  }
}